              <div class="category-icon" :style="{ backgroundColor: category.color }">
                <div class="icon-placeholder">{{ category.name.charAt(0) }}</div>
              </div>
              <div class="category-details" @click="toggleCategory(category)">
                <h3>{{ category.name }}</h3>
                <span v-if="hasMissingRequiredFields(category)" class="missing-indicator">有未填写项</span>
              </div>
              <div class="category-actions">
                <button class="edit-btn" @click.stop="editCategory(catIndex)">
                  <svg viewBox="0 0 24 24" width="16" height="16">
                    <path d="M20.71,7.04C21.1,6.65 21.1,6 20.71,5.63L18.37,3.29C18,2.9 17.35,2.9 16.96,3.29L15.12,5.12L18.87,8.87M3,17.25V21H6.75L17.81,9.93L14.06,6.18L3,17.25Z"/>
                  </svg>
                </button>
                <button class="clear-btn" @click.stop="showClearCategoryConfirm(catIndex)">
                  <svg viewBox="0 0 24 24" width="16" height="16">
                    <path d="M19,4H15.5L14.5,3H9.5L8.5,4H5V6H19M6,19A2,2 0 0,0 8,21H16A2,2 0 0,0 18,19V7H6V19Z"/>
                  </svg>
                </button>
              </div>
              <div class="category-arrow" @click="toggleCategory(category)">
                <svg viewBox="0 0 24 24" width="20" height="20" :style="{ transform: category.expanded ? 'rotate(180deg)' : 'rotate(0deg)' }">
                  <path d="M7.41,8.58L12,13.17L16.59,8.58L18,10L12,16L6,10L7.41,8.58Z"/>
                </svg>
              </div>
            </div>
            
            <transition name="slide">
              <div class="category-features" v-show="category.expanded">
                <div class="features-grid">
                  <div 
                    v-for="(feature, featIndex) in category.features" 
                    :key="feature.id"
                    class="feature-item"
                    :class="{ 'missing-field': isFeatureMissing(feature) }"
                  >
                    <div class="feature-content">
                      <div v-if="feature.imageUrl" class="feature-image">
                        <img :src="feature.imageUrl">
                      </div>
                      <div v-else class="feature-icon" :style="{ backgroundColor: feature.color }">
                        {{ feature.name.charAt(0) }}
                      </div>
                      <div class="feature-info">
                        <h4>{{ feature.name }}</h4>
                        <div v-if="feature.coordinates" class="coordinates">
                          <span :class="{ 'missing-value': !feature.coordinates.x }">X: {{ feature.coordinates.x || '未填写' }}</span>
                          <span :class="{ 'missing-value': !feature.coordinates.y }">Y: {{ feature.coordinates.y || '未填写' }}</span>
                        </div>
                        <div v-else-if="!feature.imageUrl" class="missing-value">
                          图片未上传
                        </div>
                      </div>
                    </div>
                    <div class="feature-actions">
                      <button class="edit-btn" @click.stop="editFeature(catIndex, featIndex)">
                        <svg viewBox="0 0 24 24" width="14" height="14">
                          <path d="M20.71,7.04C21.1,6.65 21.1,6 20.71,5.63L18.37,3.29C18,2.9 17.35,2.9 16.96,3.29L15.12,5.12L18.87,8.87M3,17.25V21H6.75L17.81,9.93L14.06,6.18L3,17.25Z"/>
                        </svg>
                        <span>编辑</span>
                      </button>
                      <button class="clear-btn" @click.stop="showClearFeatureConfirm(catIndex, featIndex)">
                        <svg viewBox="0 0 24 24" width="14" height="14">
                          <path d="M19,4H15.5L14.5,3H9.5L8.5,4H5V6H19M6,19A2,2 0 0,0 8,21H16A2,2 0 0,0 18,19V7H6V19Z"/>
                        </svg>
                        <span>清空</span>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </transition>
          </div>
        </div>
      </div>
    </main>

    <!-- 底部操作按钮 -->
    <footer class="bottom-bar">
      <button class="action-btn" @click="showClearAllConfirm">
        <svg viewBox="0 0 24 24">
          <path d="M19,4H15.5L14.5,3H9.5L8.5,4H5V6H19M6,19A2,2 0 0,0 8,21H16A2,2 0 0,0 18,19V7H6V19Z"/>
        </svg>
        清空
      </button>
      <button class="action-btn primary" @click="validateAndSave">
        <svg viewBox="0 0 24 24">
          <path d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z"/>
        </svg>
        保存
      </button>
    </footer>

    <!-- 遮罩层 -->
    <transition name="fade">
      <div v-if="showPicker || showConfirmDialog" class="modal-mask" @click.self="cancelPicker"></div>
    </transition>

    <!-- 底部选择器弹窗 -->
    <transition name="slide-up">
      <div v-if="showPicker" class="bottom-picker">
        <div class="picker-header">
          <button class="picker-cancel" @click="cancelPicker">取消</button>
          <h3 class="picker-title">{{ pickerTitle }}</h3>
          <button class="picker-confirm" @click="confirmPicker">确定</button>
        </div>
        <div class="picker-content">
          <div 
            v-for="(item, index) in pickerItems" 
            :key="item.id || index" 
            class="picker-item"
            :class="{ active: isPickerItemActive(item) }"
            @click="selectPickerItem(item)"
          >
            {{ item.label || item }}
          </div>
        </div>
      </div>
    </transition>

    <!-- 确认对话框 -->
    <transition name="fade">
      <div v-if="showConfirmDialog" class="confirm-dialog">
        <div class="dialog-content">
          <h3 class="dialog-title">{{ confirmTitle }}</h3>
          <p class="dialog-message">{{ confirmMessage }}</p>
          <div class="dialog-actions">
            <button class="dialog-btn cancel" @click="cancelConfirm">取消</button>
            <button class="dialog-btn confirm" @click="executeConfirmAction">确定</button>
          </div>
        </div>
      </div>
    </transition>

    <!-- 通知提示 -->
    <transition name="fade">
      <div v-if="showToast" class="toast" :class="toastType">
        <svg class="toast-icon" viewBox="0 0 24 24">
          <path v-if="toastType === 'success'" d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z" />
          <path v-if="toastType === 'error'"
            d="M19,6.41L17.59,5L12,10.59L6.41,5L5,6.41L10.59,12L5,17.59L6.41,19L12,13.41L17.59,19L19,17.59L13.41,12L19,6.41Z" />
        </svg>
        <span class="toast-message">{{ toastMessage }}</span>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'MobileGamePage',
  data() {
    return {
      showPicker: false,
      pickerTitle: '',
      pickerItems: [],
      pickerType: '',
      tempPickerValue: null,
      showToast: false,
      toastMessage: '',
      toastType: 'success',
      showConfirmDialog: false,
      confirmTitle: '',
      confirmMessage: '',
      pendingAction: null,
      pendingActionArgs: [],
      categories: [
        {
          id: 'settings',
          name: '系统设置',
          color: '#e3f2fd',
          expanded: false,
          features: [
            { 
              id: 'sound', 
              name: '音效设置', 
              color: '#4a6bff',
              coordinates: { x: 100, y: 200 }
            },
            { 
              id: 'graphics', 
              name: '画面设置', 
              color: '#4a6bff',
              imageUrl: 'https://via.placeholder.com/150x100/4a6bff/ffffff?text=画面设置'
            },
            { 
              id: 'controls', 
              name: '控制设置', 
              color: '#4a6bff',
              coordinates: { x: null, y: null }
            }
          ]
        },
        {
          id: 'communication',
          name: '社交功能',
          color: '#e8f5e9',
          expanded: false,
          features: [
            { 
              id: 'mail', 
              name: '邮件系统', 
              color: '#4caf50',
              imageUrl: 'https://via.placeholder.com/150x100/4caf50/ffffff?text=邮件系统'
            },
            { 
              id: 'chat', 
              name: '聊天功能', 
              color: '#4caf50',
              coordinates: { x: 150, y: 250 }
            },
            { 
              id: 'friends', 
              name: '好友系统', 
              color: '#4caf50',
              imageUrl: null
            }
          ]
        },
        {
          id: 'activities',
          name: '日常活动',
          color: '#fff8e1',
          expanded: false,
          features: [
            { 
              id: 'daily', 
              name: '每日签到', 
              color: '#ff9800',
              coordinates: { x: 200, y: 300 }
            },
            { 
              id: 'quests', 
              name: '每日任务', 
              color: '#ff9800',
              imageUrl: 'https://via.placeholder.com/150x100/ff9800/ffffff?text=每日任务'
            },
            { 
              id: 'events', 
              name: '限时活动', 
              color: '#ff9800',
              coordinates: { x: null, y: null }
            }
          ]
        }
      ]
    }
  },
  methods: {
    // 导航
    goBack() {
      this.$goBackOrHome()
    },
    toggleCategory(category) {
      category.expanded = !category.expanded;
    },
    editCategory(index) {
      console.log('编辑分类:', this.categories[index]);
      this.$router.push({ path: '/cropping', query: { id: 0 } });
    },
    editFeature(catIndex, featIndex) {
      console.log('编辑功能项:', this.categories[catIndex].features[featIndex]);
      this.$router.push({ path: '/mark', query: { id: 0 } });
    },
    
    // 检查是否有必填项未填写
    hasMissingRequiredFields(category) {
      return category.features.some(feature => this.isFeatureMissing(feature));
    },
    
    isFeatureMissing(feature) {
      // 如果有坐标字段，检查是否都填写了
      if (feature.coordinates) {
        return feature.coordinates.x === null || feature.coordinates.y === null;
      }
      // 如果没有坐标字段，检查是否有图片
      if (!feature.imageUrl) {
        return true;
      }
      return false;
    },
    
    // 验证并保存
    validateAndSave() {
      const missingCategories = this.categories.filter(category => 
        this.hasMissingRequiredFields(category)
      );
      
      if (missingCategories.length > 0) {
        // 展开所有有问题的分类
        missingCategories.forEach(cat => {
          cat.expanded = true;
        });
        
        this.showNotification('请先填写所有必填项', 'error');
        return;
      }
      
      this.saveAll();
    },
    
    // 显示确认对话框
    showClearCategoryConfirm(index) {
      this.confirmTitle = '清空分类内容';
      this.confirmMessage = '确定要清空此分类下的所有坐标和图片吗？';
      this.pendingAction = this.clearCategory;
      this.pendingActionArgs = [index];
      this.showConfirmDialog = true;
    },
    
    showClearFeatureConfirm(catIndex, featIndex) {
      this.confirmTitle = '清空功能项';
      this.confirmMessage = '确定要清空此项的坐标或图片吗？';
      this.pendingAction = this.clearFeature;
      this.pendingActionArgs = [catIndex, featIndex];
      this.showConfirmDialog = true;
    },
    
    showClearAllConfirm() {
      this.confirmTitle = '清空所有内容';
      this.confirmMessage = '确定要清空所有坐标和图片吗？';
      this.pendingAction = this.clearAll;
      this.pendingActionArgs = [];
      this.showConfirmDialog = true;
    },
    
    // 执行确认操作
    executeConfirmAction() {
      if (this.pendingAction) {
        this.pendingAction(...this.pendingActionArgs);
      }
      this.showConfirmDialog = false;
    },
    
    cancelConfirm() {
      this.showConfirmDialog = false;
    },
    
    // 清空操作（只清空值，不删除参数）
    clearCategory(index) {
      this.categories[index].features.forEach(feature => {
        if (feature.coordinates) {
          feature.coordinates.x = null;
          feature.coordinates.y = null;
        }
        if (feature.imageUrl) {
          feature.imageUrl = null;
        }
      });
      this.showNotification('分类内容已清空', 'success');
    },
    
    clearFeature(catIndex, featIndex) {
      const feature = this.categories[catIndex].features[featIndex];
      if (feature.coordinates) {
        feature.coordinates.x = null;
        feature.coordinates.y = null;
      }
      if (feature.imageUrl) {
        feature.imageUrl = null;
      }
      this.showNotification('内容已清空', 'success');
    },
    
    clearAll() {
      this.categories.forEach(category => {
        category.features.forEach(feature => {
          if (feature.coordinates) {
            feature.coordinates.x = null;
            feature.coordinates.y = null;
          }
          if (feature.imageUrl) {
            feature.imageUrl = null;
          }
        });
      });
      this.showNotification('所有内容已清空', 'success');
    },
    
    saveAll() {
      // 这里可以添加保存到服务器的逻辑
      console.log('保存数据:', this.categories);
      this.showNotification('数据保存成功', 'success');
    },
    
    openPicker(type) {
      // 示例：打开选择器
      this.pickerType = type;
      this.pickerTitle = '选择项';
      this.pickerItems = ['选项1', '选项2', '选项3'];
      this.showPicker = true;
    },
    
    isPickerItemActive(item) {
      return this.tempPickerValue === item;
    },
    
    selectPickerItem(item) {
      this.tempPickerValue = item;
    },
    
    confirmPicker() {
      // 处理选择器确认
      this.showPicker = false;
      this.showNotification('选择已确认', 'success');
    },
    
    cancelPicker() {
      this.showPicker = false;
    },
    
    showNotification(message, type = 'success') {
      this.toastMessage = message;
      this.toastType = type;
      this.showToast = true;
      
      setTimeout(() => {
        this.showToast = false;
      }, 3000);
    }
  }
}
</script>

